// 引入通用table组件
import { showErrorNotify } from '@/utils/notify';
import { showWarningMessage } from '@/utils/message';
import { BTN_TEXT, STATUS_CODE, TITLE } from '@/utils/constantsFile';
import { debounce } from '@/utils/debounce';
import {
  deviceExceptionGetDeviceWarnStatisticsAnalyze
} from '@/api/warn';
export default {
  components: {
  },
  props: {
    type: Number
  },
  data() {
    return {
      BTN_TEXT: BTN_TEXT,
      myChart: null,
      analyzeModel: 1,
      // analyzeName: '报警计数',
      analyzeOptions: [
        { name: '报警计数', value: 'WARN_COUNT' },
        { name: '报警时长', value: 'WARN_TIME' }
      ],
      xModel: 3,
      // xName: '报警代码',
      xOptions: [
        { name: '报警代码', value: 'WARN_CODE' },
        { name: '报警类型', value: 'WARN_TYPE' },
        { name: '报警时间', value: 'WARN_DAY' },
        { name: '报警设备', value: 'WARN_DEVICE' }
      ],
      dateTime: [],
      pickerOptions: {
        shortcuts: [
          {
            text: BTN_TEXT.LAST_WEEK,
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
            }
          },
          {
            text: BTN_TEXT.LAST_MONTH,
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          },
          {
            text: BTN_TEXT.LAST_THREE_MONTHS,
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit('pick', [start, end]);
            }
          }
        ]
      },
      // 保存搜索内输入的搜索条件，防止翻页时变回无条件查询
      savedSearchCondition: []
    };
  },
  computed: {

  },
  watch: {

  },
  created() {
    this.$nextTick(() => {
      this.getData();
      const end = new Date();
      const start = new Date();
      start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
      this.dateTime = [
        this.$utils.toDateString(start, 'yyyy-MM-dd') + ' 00:00:00',
        this.$utils.toDateString(end, 'yyyy-MM-dd') + ' 23:59:59'
      ];
    });
  },
  methods: {
    reset() {
      const end = new Date();
      const start = new Date();
      start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
      this.dateTime = [
        this.$utils.toDateString(start, 'yyyy-MM-dd') + ' 00:00:00',
        this.$utils.toDateString(end, 'yyyy-MM-dd') + ' 23:59:59'
      ];
      this.xModel = 0;
      this.analyzeModel = 0;
      this.getData();
    },
    async getData() {
      try {
        // this.loading = true;
        var contentSearch = {};
        contentSearch.conditionJson1 = [];
        contentSearch.conditionJson1.push({
          column: 'startTime',
          value: this.dateTime[0]
        });
        contentSearch.conditionJson1.push({
          column: 'endTime',
          value: this.dateTime[1]
        });

        const res = await deviceExceptionGetDeviceWarnStatisticsAnalyze({
          deviceWarnStatisticalAbscissaEnum: this.xOptions[this.xModel].value,
          deviceWarnStatisticalIndicatorsEnum: this.analyzeOptions[this.analyzeModel].value
        }, contentSearch);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          var bfbArr = [];
          var xValue = [];
          var yValue = [];
          for (var i = 0; i < res.rows.length; i++) {
            if (res.rows[i].otherY) {
              bfbArr.push(Number((res.rows[i].otherY * 100).toFixed(2)));
            } else {
              bfbArr.push(0);
            }

            if (res.rows[i].y) {
              yValue.push(res.rows[i].y);
            } else {
              yValue.push(0);
            }

            if (this.xOptions[this.xModel].value === 'WARN_CODE' || this.xOptions[this.xModel].value === 'WARN_DAY') {
              xValue.push(res.rows[i].x);
            } else {
              xValue.push(res.rows[i].otherX1);
            }
          }
          this.draw(bfbArr, xValue, yValue);
        } else {
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    draw(bfbArr, xValue, yValue) {
      this.myChart = this.$echarts.init(this.$refs.echartGantt);
      this.myChart.setOption({
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'cross',
            crossStyle: {
              color: '#999'
            }
          },
          formatter: (params) => {
            let tip = params[0].name;
            for (let i = 0; i < params.length; i++) {
              if (i === 0) {
                if (this.analyzeOptions[this.analyzeModel].value === 'WARN_TIME') {
                  tip += '<br>' + params[i].seriesName + ': ' + this.timeStampSeconds(params[i].value);
                } else {
                  tip += '<br>' + params[i].seriesName + ': ' + params[i].value + '次';
                }
              } else {
                tip += '<br>' + params[i].seriesName + ': ' + params[i].value + '%';
              }
            }
            return tip;
          }
        },
        legend: {
          data: [this.xOptions[this.xModel].name, this.analyzeOptions[this.analyzeModel].name, '累计占比'],
          right: 10
        },
        grid: {
          containLabel: true,

          left: '3%',
          right: '5%'
        },
        dataZoom: [
          {
            type: 'slider',
            filterMode: 'weakFilter',
            top: 'bottom'
          },
          {
            type: 'inside',
            filterMode: 'weakFilter'
          }
        ],
        xAxis: [
          {
            type: 'category',
            name: this.xOptions[this.xModel].name,
            data: xValue,
            axisPointer: {
              type: 'shadow'
            },
            nameGap: 30
            // nameTextStyle: {
            //   align: 'center',
            //   verticalAlign: 'top'
            // }
          }
        ],
        yAxis: [
          {
            type: 'value',
            name: this.analyzeOptions[this.analyzeModel].name,
            // min: 0,
            // max: 250,
            // interval: 50,
            axisLabel: {
              // formatter: '{value} lm'
            }
          },
          {
            type: 'value',
            name: '累计占比',
            // min: 0,
            // max: 100,
            interval: 5,
            axisLabel: {
              formatter: '{value} %'
            }
          }
        ],
        series: [
          {
            name: this.analyzeOptions[this.analyzeModel].name,
            type: 'bar',
            tooltip: {
              valueFormatter: (value) => {
                return value;
              }
            },
            barWidth: '30',
            itemStyle: {
              normal: {
                barBorderRadius: [15, 15, 15, 15],
                color: new this.$echarts.graphic.LinearGradient(0, 1, 0, 0, [{
                  offset: 0,
                  color: '#FC5454' // 0% 处的颜色
                }, {
                  offset: 1,
                  color: '#E50404' // 100% 处的颜色
                }], false)
              }
            },
            data: yValue
          },
          {
            name: '累计占比',
            type: 'line',
            yAxisIndex: 1,
            smooth: true,
            symbolSize: 10,
            tooltip: {
              valueFormatter: (value) => {
                return value + ' %';
              }
            },
            lineStyle: {
              width: 5
            },
            itemStyle: {
              normal: {
                color: '#0871DC'
              }
            },
            data: bfbArr
          }
        ]
      });
      const handleResize = debounce(this.myChart.resize, 300);
      window.addEventListener('resize', () => {
        handleResize();
      });
    },
    search() {
      this.getData();
    },
    timeStampSeconds(cellValue) {
      var day = parseInt(cellValue / 60 / 60 / 24);
      var hour = parseInt(cellValue / 60 / 60 % 24);
      var min = parseInt(cellValue / 60 % 60);
      var s = parseInt(cellValue % 60);
      cellValue = '';
      if (day > 0) {
        cellValue = day + '天';
      }
      if (hour > 0) {
        cellValue += hour + '小时';
      }
      if (min > 0) {
        cellValue += parseFloat(min) + '分钟';
      }
      if (s > 0) {
        cellValue += parseFloat(s) + '秒';
      }
      return cellValue;
    }
  }
};
